body {
  font-family: "Press Start 2P", sans-serif;
  background: linear-gradient(to bottom, #ddd, #aaa);
  color: black;
  text-align: center;
}

.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.start-buttons button {
  margin: 10px;
  padding: 10px 20px;
  background-color: black;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 14px;
  border-radius: 5px;
}

.game-timers {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 500px;
  margin: 20px auto;
}

.controls {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 500px;
}

.large-button {
  font-size: 18px;
  padding: 20px;
  background-color: #e00;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
